import React from 'react';
import './btn.styles.scss';
import Aos from 'aos';
import 'aos/dist/aos.css';
const Btn = ({color, text, SendMail, fadeUp}) => {
    Aos.init({duration:500});

    return (
        <button data-aos={fadeUp ? "fade-up" : null} className={`btn btn-${color}`} onClick={SendMail ? SendMail : null}>
        {text}
        </button>
    )
}
 
    


export default Btn;