import React from 'react';
import './App.scss';
import Header from './components/header/header.components';
import AboutSection from './components/about-section/about-section.component';
import LoadingAnimation from './components/loading-animation/loadingAnimation.component';
import ProjectsSection from './components/projects-section/projects-section.component';
import projects from './data/projects-data';
import ContactSection from './components/contact-section/contact-section.components';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state= {
      isLoading: true,
      projects
    }
  }


  componentDidMount  (){
     setTimeout(()=> {    this.setState({isLoading: false})
    } ,2000)
  };
  render(){
    return (
      this.state.isLoading ? <LoadingAnimation/> : (
        <div>
          <Header/>
          <AboutSection/>
          <ProjectsSection projects={this.state.projects}/>
          <ContactSection/>
        </div>
      )
      
    );
  }

}
export default App;
