import React, { Component } from 'react';
import Logo from '../../assets/logo.png';
import './header-nav.styles.scss';
import Scrollchor from 'react-scrollchor';
import Burger from 'react-css-burger';


class HeaderNav extends Component {

    constructor(props){
        super(props);

        this.state = {
            prevScrollpos: window.pageYOffset,
            visible: true,
            active: false
        }
    }
    

    handleScroll = () => {
        const { prevScrollpos } = this.state;
      
        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;
      
        this.setState({
          prevScrollpos: currentScrollPos,
          visible,
          active:false
        });
      };
      componentDidMount() {
        window.addEventListener("scroll", this.handleScroll);
      }
      
      componentWillUnmount() {
        window.removeEventListener("scroll", this.handleScroll);
      }
      scrollToRef = (ref) => window.scrollTo(0, ref.current.offsetTop)   

    render(){
        const setOverflow = () => this.state.active ? document.body.style.overflow = 'hidden' : document.body.style.overflow = 'visible'
        setOverflow()
        return (
            <div className={`header-nav ${!this.state.visible ? 'header-nav--hidden' : ''} ${(this.state.prevScrollpos) ? 'header-nav--white' : ''}`}>
                <div className='header-nav__logo'>
                    <img className='header-nav__image' alt='logo' src={Logo}/>
                </div>
                <nav className='header-nav__menu'>
                    <ul className={`header-nav__list ${(this.state.prevScrollpos) ? 'header-nav__list--whiteStyle' : ''}`}>
                        <li className='header-nav__item header-nav__item--1'>
                        <Scrollchor  beforeAnimate={() => this.setState({active:false})} to="#about" animate={{offset: 20, duration: 600}}  className='header-nav__link'>About</Scrollchor>
                        </li>
                        <li className='header-nav__item header-nav__item--2'>
                        <Scrollchor to="#projects" animate={{offset: 20, duration: 600}}  className='header-nav__link' href='#'>Projects</Scrollchor>
                        </li>
                        <li className='header-nav__item header-nav__item--3'>
                        <Scrollchor to="#contact" animate={{offset: 20, duration: 600}}  className='header-nav__link' href='#'>Contact</Scrollchor>
                        </li>
                   
                    </ul>
                    <button onClick={() => window.open(`http://abdellahmaarifa.github.io/portfolio/resume.pdf`)} className={`header-nav__btn  ${(this.state.prevScrollpos) ? 'header-nav__btn--whiteStyle' : ''}`}>resume</button>

                </nav>
                <Burger
                onClick={() => this.setState({ active: !this.state.active })}
                active={this.state.active}
                burger="emphatic"
                color="#55c57a"
                hoverOpacity={0.8}
                scale={1}
              />

              <div className={`menuBar ${this.state.active ? 'menuBar__active' : 'menuBar__inactive'}`}>
              <nav className='menuBar__menu'>
              <ul className={`menuBar__list ${(this.state.prevScrollpos) ? 'menuBar__list--whiteStyle' : ''}`}>
                  <li className='menuBar__item menuBar__item--1'>
                  <Scrollchor to="#about"  className='menuBar__link'>About</Scrollchor>
                  </li>
                  <li className='menuBar__item menuBar__item--2'>
                  <Scrollchor to="#projects"  className='menuBar__link' href='#'>Projects</Scrollchor>
                  </li>
                  <li className='menuBar__item menuBar__item--3'>
                  <Scrollchor to="#contact"  className='menuBar__link' href='#'>Contact</Scrollchor>
                  </li>
             
              </ul>
              <button onClick={() => window.open(`http://abdellahmaarifa.github.io/portfolio/resume.pdf`)} className='menuBar__btn' >resume</button>

          </nav>
              </div>
            </div>
    )
    }
};

export default HeaderNav;