import React from 'react';
import './project.styles.scss';
import 'aos/dist/aos.css';

class Project extends React.Component{

    constructor(props){
        super(props);
        this.state= {
            ...this.props
        }
    };
 

    render(){
        const {name, image, buildByIcons, githubLink,liveLink,description,videoMp4,videoWebm } = this.state.project;
        const position = this.state.index %2  === 0 || this.state.index === 0 ; 
        const buildByIconsUrl = position  ?  buildByIcons.reverse() : buildByIcons;
        return (
            <div data-aos="fade-up"  className={`project__container  ${position ? 'project__container--left' : 'project__container--right'}`}>
      
             <div data-aos="fade-up"  className="project__featuredImage" style={{backgroundImage:`url(${image})`}} >
            
             </div>

                <div data-aos={`${position ? 'fade-left' : 'fade-right'}`}  className='project__details'>
                    <div className={`project__name  ${position ? 'project__name--left' : 'project__name--right'}`} >
                        <h2 className='project__title'>
                            {name}
                        </h2>
                        <div className='project__links'>
                            <a className='project__url' href={githubLink} target='_blanc'><i className="fab fa-github"></i></a>
                            <a className={`${liveLink ? 'project__url' : 'project__url project__url--hide'}`} href={liveLink} target='_blanc'><i className="fas fa-external-link-alt"></i></a>
                        </div>
                    </div>
                   
                    <p className={`project__description  ${position ? 'project__description--left' : 'project__description--right'}`}>{description}</p>
                    <div className='project__buildByIcons'>
                        {
                            buildByIconsUrl.map((el, i) => <img className='project__buildByIconsImg' alt='icon' src={el} key={i}/>)
                        }
                    </div>
                </div>
            
            </div>
    );
    }
}


export default Project;