import React from 'react';
import'./header-heading.styles.scss';
import FeaturedImg from '../../assets/featuredImg.png';
import Btn from '../btn/btn.components';
const HeaderHeading = () => (
  
    <div className='heading'>
        <div className='heading-box'>
            <p className='heading-box__grating'>Hi, My name is</p>
            <h1 className='heading-box__primary'>
                <span className='heading-box__primary--main'>Abdellah Maarifa.</span>
                <span className='heading-box__primary--sub'>I'm a web developer.</span>
            </h1>
            <p className='heading-box__description'>
            I'm a web developer based in Marrakech, Morocco specializing in 
            building exceptional and modern web sites and web web apps</p>
            <div className='heading-box__btn'>
                <Btn color='green' text='Get In Touch' SendMail={() => window.open('mailto:maarifa.abdo@gmail.com')}/>
                <Btn color='white' text='See My Work'/>
            </div>
        </div>
        <div className='heading-featuredImg'>
            <img className='heading-featuredImg__img' alt='featured img' src ={FeaturedImg} />
        </div>
    </div>
);




export default HeaderHeading;