import React from 'react';
import Profile from '../../assets/profile.jpg';
import './about-section.styles.scss';
import SocialIcons from '../social-icons/socialIcons.component';
import Aos from 'aos';
import 'aos/dist/aos.css';
const AboutSection = () => {
    Aos.init({duration:1000});
    return(
    <div className='about' id='about'>
        <div data-aos="fade-up" className='about__image-box'>
                <img className='about__img' alt='ProfileImage' src={Profile}/>
                <SocialIcons/>
        </div>
        <div className='about__description'>
            <h1 data-aos="fade-up" className='about__heading'>About Me</h1>
            <p  data-aos="fade-up" className='about__text'>
            Hi there, I'm Abdellah Maarifa. a web developer who likes to build things for the web. I spend a lot of time learning how to code and how to make beautiful and powerful web apps, I like the idea of keep learning and improving my self, and this is why I have a big desire for working with people who were in these major  from long time so I could learn more from them, I've been taken some online courses include CS50, which was the first step for me and it was Amazing.            </p>
            <p data-aos="fade-up" className='about__text'>
            I use javascript to build an entire web app with some famous libraries like react, I try to pick the right tool for the right job and I have the ability to learn new things and grasp any concept so quickly.            </p>
            <p data-aos="fade-up" className='about__text'>
            Besides programming, I like learning languages and reading books in my free time, I've been in many local Associations too, and thanks to this experience, I gain some social skills while we were trying to help others. with the skills that I gain from other fields, I can bring them into my programming path and build something that benefits others.</p>
        </div>
    </div>
)
}

export default AboutSection;