import React from 'react';
import Project from '../project/project.component';
import './projects-section.styles.scss';
import BackgroundImg from '../../assets/project-background.svg';
import Aos from 'aos';
import 'aos/dist/aos.css';
const ProjectsSection = ({projects}) => {
    Aos.init({duration:1000});
    return (
    <div className='projects' id='projects' style={{backgroundImage:`url(${BackgroundImg})`}}>
        <h1 data-aos="fade-up" className='projects__heading'>Some Things I've Built</h1>
        {
            projects.map((e, i) => (<Project project={e} index={i} key={i}/>))
        }
    </div>
);
}
export default ProjectsSection;