import React from 'react';
import Btn from '../btn/btn.components';
import SocialIcons from '../social-icons/socialIcons.component';
import './contact-section.styles.scss';
import Aos from 'aos';
import 'aos/dist/aos.css';
const ContactSection = () => {
    Aos.init({duration:500});
    return(
    <div className='contact-section' id='contact'>
       
        <div className='contact-section__contactBox'>
            <h2 data-aos="fade-up" className='contact-section__title'>Get In Touch</h2>
            <p data-aos="zoom-in" className='contact-section__description'>
            Hey, I'm currently looking for any new opportunities so if you want to hire me, please contact me, however, my inbox is always open. Whether you want to get in touch with me or you have a question or just want to say hi, I'll respond to you as soon as I can!            </p>
            <Btn className='contact-section__mailBtn' fadeUp='true' color='white' text='Say Hello' SendMail={() => window.open('mailto:maarifa.abdo@gmail.com')}/>
        </div>
        
        <div className='contact-section__footer'>
            <p className='contact-section__buildByText'>built with <span className='contact-section__buildByText--heart'>&#10084;</span> Marrakech Morocco</p>
            <SocialIcons className='contact-section__socialIcons'/>
        </div>
       
    </div>
)};



/*

const number = [1,2,3];
const letter = ['a', 't', 'y'];

const mix [number, letter]


*/ 
export default ContactSection;